import React, { useContext, useEffect } from "react";
import "./NavbarComponents.css";
import Headerheading from "./Headerheading";

import Navbars from "./Navbars";
import Contex from "../Store/Contex";
import axios from "axios";

const Header = () => {
  const contexVal = useContext(Contex);
  useEffect(() => {
    if (contexVal.isFirst) {
      const getData = async () => {
        try {
          contexVal.setIsFirst();
          const crudres = await axios.get(
            `https://ecommerce-application-95e56-default-rtdb.firebaseio.com/cart${contexVal.mail}.json`
          );
          console.log(crudres);
          if (crudres.data) {
            contexVal.addcruditems(crudres.data);
          }
        } catch (err) {
          alert("error");
        }
      };
      getData();
      return;
    }
    const postData = async () => {
      try {
        const res = await axios.put(
          `https://ecommerce-application-95e56-default-rtdb.firebaseio.com/cart${contexVal.mail}.json`,
          contexVal.items
        );
      } catch (err) {
        alert("error");
      }
    };
    postData();
  }, [contexVal.items]);

  return (
    <div>
      <Navbars></Navbars>

      <Headerheading />
    </div>
  );
};

export default Header;
